<div class="custom-container tip"><p class="custom-container-title">json() is part of express</p>
<p>Nowadays, you can use <code>express.json()</code> instead of <code>bodyParser.json()</code> to create a middleware parsing the body of incoming HTTP requests in JSON format.</p>
